import setLang from '../lang'
import Config from '../../config'

const lang = {}

lang[Config.localePrefix] = {
  locale: 'zh-CN',
  select: {
    placeholder: '',
    noMatch: '',
    loading: ''
  },
  cascader: {
    noMatch: '',
    loading: '',
    placeholder: ''
  },
  table: {
    noDataText: '',
    noFilteredDataText: '',
    confirmFilter: '',
    resetFilter: '',
    clearFilter: ''
  },
  datepicker: {
    now: '',
    today: '',
    cancel: '',
    clear: '',
    confirm: '',
    selectDate: '',
    selectTime: '',
    startDate: '',
    startTime: '',
    endDate: '',
    endTime: '',
    prevYear: '',
    nextYear: '',
    prevMonth: '',
    nextMonth: '',
    year: '',
    month1: '1 ',
    month2: '2 ',
    month3: '3 ',
    month4: '4 ',
    month5: '5 ',
    month6: '6 ',
    month7: '7 ',
    month8: '8 ',
    month9: '9 ',
    month10: '10 ',
    month11: '11 ',
    month12: '12 ',
    // week: '',
    weeks: {
      sun: '',
      mon: '',
      tue: '',
      wed: '',
      thu: '',
      fri: '',
      sat: ''
    },
    months: {
      jan: '',
      feb: '',
      mar: '',
      apr: '',
      may: '',
      jun: '',
      jul: '',
      aug: '',
      sep: '',
      oct: '',
      nov: '',
      dec: ''
    }
  },
  transfer: {
    titles: {
      source: '',
      target: ''
    },
    filterPlaceholder: '',
    notFoundText: ''
  },
  dialog: {
    info: '',
    success: '',
    warning: '',
    error: '',
    okText: '',
    cancelText: ''
  },
  popover: {
    okText: '',
    cancelText: ''
  },
  pagination: {
    prevText: '',
    nextText: '',
    total: '',
    item: '',
    items: '',
    pageSize: '/',
    goto: '',
    pageText: '',
    prev5Text: '5',
    next5Text: '5'
  },
  rate: {
    star: '',
    stars: ''
  },
  tree: {
    emptyText: ''
  },
  loading: {
    loadingText: '...'
  },
  upload: {
    deleteTip: ' delete ',
    delete: '',
    preview: '',
    continue: ''
  },
  charts: {
    noDataText: '',
    otherText: ''
  }
}

setLang(lang)

export default lang



